import { Injectable } from '@angular/core';
import { HttpClient, HttpRequest, HttpEvent, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { WebApiManager } from '../../shared/services/webApiManager.service';
import { AppSettings } from '../../../app/appSettings.setting';

@Injectable()
export class UploadFileService {
  public static OpportunityUploadUrl = AppSettings.BaseAPIUrl + 'Fileupload/';
  constructor(private webApiService: WebApiManager, private http: HttpClient) { }

  pushFileToStorage(file: File, fileUploadModel: any): Observable<HttpEvent<{}>> {
    let formdata: FormData = new FormData();
    formdata.append('file', file);
    formdata.append('filetype', fileUploadModel.fileType);

    const req = new HttpRequest('POST', UploadFileService.OpportunityUploadUrl, formdata, {
      reportProgress: true,
      responseType: 'text'
    });

    return this.http.request(req);
  }
}



