import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { ReactiveFormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { UploadFileRouting } from './upload-file.routing';
import { SharedModule } from '../../shared/shared.module';

import { HttpClientModule } from '@angular/common/http';

import { UploadFileComponent } from './upload-file.component';
import { DetailsUploadComponent } from './details-upload/details-upload.component';
import { FormUploadComponent } from './form-upload/form-upload.component';
import { ListUploadComponent } from './list-upload/list-upload.component';
import { UploadFileService } from './upload-file.service';

@NgModule({
  imports: [HttpClientModule, CommonModule, FormsModule, SharedModule, RouterModule, UploadFileRouting, ReactiveFormsModule],
  declarations: [UploadFileComponent, DetailsUploadComponent, FormUploadComponent, ListUploadComponent],
  exports: [UploadFileComponent, DetailsUploadComponent, FormUploadComponent, ListUploadComponent],
  entryComponents: [UploadFileComponent, DetailsUploadComponent, FormUploadComponent, ListUploadComponent],
  providers: [UploadFileService]
})
export class UploadFileModule { }



