import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpResponse, HttpEventType } from '@angular/common/http';
import { UploadFileService } from '../upload-file.service';

@Component({
  selector: 'form-upload',
  templateUrl: './form-upload.component.html',
  styleUrls: ['./form-upload.component.css']
})
export class FormUploadComponent implements OnInit {
  public currentFileType: {};
  public fileTypes: any[];
  private value: any = {};
  selectedFiles: FileList;
  currentFileUpload: File;  
  progress: { percentage: number } = { percentage: 0 }

  constructor(private uploadService: UploadFileService) { }

  ngOnInit() {

    this.fileTypes = [
      { name: 'Opportunity Data', type: 'opportunity' },
      { name: 'SR Data', type: 'srdata' },
      { name: 'Excalibur Data', type: 'excaliburdata' }
    ]
    this.currentFileType = this.fileTypes[0];
  }

  selectFile(event) {
    this.selectedFiles = event.target.files;
  }

  upload() {
    this.progress.percentage = 0;

    this.currentFileUpload = this.selectedFiles.item(0)
    let fileUploadModel = {
      fileType: this.currentFileType['type']
    };

    this.uploadService.pushFileToStorage(this.currentFileUpload, fileUploadModel).subscribe(event => {
      if (event.type === HttpEventType.UploadProgress) {
        this.progress.percentage = Math.round(100 * event.loaded / event.total);
      } else if (event instanceof HttpResponse) {
        //console.log('File is completely uploaded!');
      }
    })

    this.selectedFiles = undefined
  }
}



